.social-icon {
  width: 44px;
  z-index: 100;
  display: block;
  position: fixed;
  top: 50%;
  right: 20px;
  transform: translateY(-52%);
}

.social-icon.is-hiden .one {
  transform: translateY(50%);
}

.social-icon.is-hiden .two {
  z-index: -1;
  opacity: 0;
  pointer-events: none;
}

.social-icon.is-hiden .social-icon-more {
  transform: translateY(-427%);
}

.social-icon-list {
  flex-direction: column;
  gap: 16px;
  padding-top: 16px;
  display: flex;
}

.social-icon-list.one {
  padding-top: 0;
  transition: transform .5s linear;
  transform: translateY(0);
}

.social-icon-list.two {
  opacity: 1;
  transition: opacity .25s linear .25s;
}

.social-icon-list a {
  cursor: pointer;
  color: #fff;
  border-radius: 50px;
  margin: 0;
}

.social-icon-list a:hover .social-icon-list-svg, .social-icon-list a:focus .social-icon-list-svg {
  filter: drop-shadow(0 0 10px #16dcf2);
}

.social-icon-list-svg {
  width: 44px;
  height: 44px;
  fill: currentColor;
  margin-left: auto;
  margin-right: auto;
  transition: filter .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.social-icon-more {
  transition: transform .5s linear;
  position: absolute;
  bottom: -60px;
  right: 0;
  transform: translateY(0);
}

.social-icon-more svg {
  cursor: pointer;
}

.social-icon-more svg use {
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1);
}

.social-icon-more svg .social-icon-more-hide {
  opacity: 0;
}

.social-icon-more:hover, .social-icon-more:focus {
  filter: drop-shadow(0 0 10px #16dcf2);
}

.social-icon-more:hover .social-icon-more-hide, .social-icon-more:focus .social-icon-more-hide {
  opacity: 1;
}

.social-icon-more:hover .social-icon-more-more, .social-icon-more:focus .social-icon-more-more {
  opacity: 0;
}

